<template>
  <div v-if="!item.hidden" class="item-wrapper">
   
    <template
      v-if="hasOneShowingChild(item.children,item)&&(!onlyChild.children||onlyChild.noShowingChild)&&!item.alwaysShow"
    >
      <el-menu-item :index="getPath">
        <font-awesome-icon v-if="onlyChild.meta.icon" :icon="onlyChild.meta.icon" size="lg" fixed-width />
        <template slot="title">{{onlyChild.meta.title}}</template>
      </el-menu-item>
    </template>
    <template v-else>
      <el-submenu :index="getPath">
        <template slot="title">
          <font-awesome-icon v-if="item.meta.icon" :icon="item.meta.icon" size="lg" fixed-width />
          <span slot="title">{{ item.meta.title }}</span>
        </template>

          <menu-item
            v-for="child in item.children"
            :key="child.path"
            :item="child"
            :basePath="item.path"
          ></menu-item>

      </el-submenu>
    </template>
  </div>
</template>

<script>
export default {
  name: "menuItem",
  props: {
    item: {
      type: Object,
      required: true
    },
    basePath: {
      type: String,
      default: ""
    }
  },
  data(){
    this.onlyChild=null
    return{
    }
  },
  computed: {
    getPath() {
      if (this.item.redirect) {
        return this.item.redirect;
      }
      if (this.item.path.includes(":")) {
        return this.basePath + "/" + this.item.path.split(":")[0];
      } else {
        return this.basePath + "/" + this.item.path;
      }
    }
  },
  methods:{
    hasOneShowingChild(children=[],parent){
      const showingChildren = children.filter(item=>{
        if(item.hidden){
          return false
        }else{
          this.onlyChild = item
          return true
        }
      })
        if(showingChildren.length===0){
          this.onlyChild={...parent,path:'',noShowingChild:true}
          return true
        }
        if(showingChildren.length===1){
          return true
        }
        return false
    }
  }
};
</script>