<template>
  <el-breadcrumb separator-class="el-icon-arrow-right">
    <el-breadcrumb-item v-for="(item,index) in levelList" :key="item.path">
      <span v-if="item.redirect||index===levelList.length-1">{{item.meta.title}}</span>
      <a v-else @click.prevent="handlePath(item)">{{item.meta.title}}</a>
    </el-breadcrumb-item>
  </el-breadcrumb>
</template>

<script>
const { compile } = require("path-to-regexp") ;
export default {
  name: "breadcrumb",
  data() {
    return {
      levelList: null
    };
  },
  watch: {
    $route() {
      this.getLevelList();
    }
  },
  created() {
    this.getLevelList();
  },
  methods: {
    getLevelList() {
      let matched = this.$route.matched.filter(
        item => item.meta && item.meta.title
      );
      let first = matched[0];
      if (!this.isDashboard(first)) {
        matched = [{ path: "/", meta: { title: "主页" } }].concat(
          matched
        );
      }
      this.levelList = matched.filter(item => item.meta && item.meta.title);
    },

    isDashboard(route) {
      const name = route && route.name;
      if (!name) {
        return;
      }
      return (
        name.trim().toLocaleLowerCase() === "userIndex".toLocaleLowerCase()
      );
    },

    pathCompile(path) {
      const { params } = this.$route;
      var toPath = compile(path);
      return toPath(params);
    },
    handlePath(item) {
      const { path, redirect } = item;
      if (redirect) {
        this.$router.push(redirect);
        return;
      }  
      this.$router.push(this.pathCompile(path));
    }
  }
};
</script>

<style lang="scss" scoped>
.el-breadcrumb{
    display: inline-block;
    font-size: 15px;
}
</style>