const state = {
	isCollapse: false,
	device: '',
	animate: true,
}

const mutations = {
	SET_SIDEBAR(state, flag) {
		if (state.animate === false) {
			state.animate = true
		}
		state.isCollapse = flag
	},
	SET_DEVICE(state, device) {
		state.device = device
	},
	SET_ANIMATE(state, animate) {
		state.animate = animate
	}
}

const actions = {
	collapseSidebar({ commit }) {
		commit('SET_SIDEBAR', true)
	},
	openSidebar({ commit }) {	
		commit('SET_SIDEBAR', false)
	},
	setDevice({ commit }, device) {
		commit('SET_DEVICE', device)
		if (device === 'mobile') {
			commit('SET_SIDEBAR', true)
		}
		commit('SET_ANIMATE', false)
	}
}

export default {
	namespaced: true,
	state,
	mutations,
	actions
}