<template>
  <el-container class="main-container" :class="{'no-animate':!animate}">
    <div class="app-wrap" :class="{'mobile-close':device==='mobile'&&!isCollapse}">
      <div v-if="device ==='mobile'&&!isCollapse" class="sidebar-bg" @click="handleClick"></div>
      <div
        class="sidebar-wrap"
        :class="{'mobile-collapse':device==='mobile'&&isCollapse,'collapse':isCollapse}"
      >
        <side-bar></side-bar>
      </div>
      <el-container class="main-wrapper" direction="vertical">
        <page-header></page-header>
        <el-main direction="horizontal">
          <router-view />
        </el-main>
        <el-footer>
          <span>©{{ date }} ech0o</span>
          <span>
            <a href="https://github.com/ech0o">
              <font-awesome-icon :icon="['fab','github']" size="lg" class="github-icon"></font-awesome-icon>
            </a>
          </span>
        </el-footer>
      </el-container>
    </div>
  </el-container>
</template>
<script>
import SideBar from "./SideBar";
import store from "@/store/index";
import pageHeader from "./pageHeader";
export default {
  name: "Layout",
  components: { SideBar, pageHeader },
  beforeMount() {
    window.addEventListener("resize", this.getDevice);
  },
  beforeDestroy() {
    window.removeEventListener("resize", this.getDevice);
  },
  created() {
    this.getDevice();
  },
  watch: {
    $route() {
      if (this.device === "mobile" && !this.isCollapse) {
        this.$store.dispatch("sidebar/collapseSidebar");
      }
    }
  },
  computed: {
    date: function() {
      return new Date().getFullYear();
    },

    isCollapse() {
      return store.getters.sidebar;
    },
    device() {
      return store.getters.device;
    },
    animate() {
      return store.getters.animate;
    }
  },
  data() {
    return {
      title: this.$route.path,
      user: store.getters.name
    };
  },
  methods: {
    handleClick() {
      this.$store.dispatch("sidebar/collapseSidebar");
    },
    getDevice() {
      if (window.innerWidth <= 768) {
        this.$store.dispatch("sidebar/setDevice", "mobile");
      } else {
        this.$store.dispatch("sidebar/setDevice", "desktop");
      }
    }
  }
};
</script>
<style lang="scss" scoped>
