  padding-top: 10px;
}

.app-wrap{
  width: 100%;
}
.mobile-close{
  position: fixed;
}

.main-container {
  height: 100%;
}

.main-wrapper {
  min-height: 100vh;
}

.github-icon {
  margin-left: 10px;
}

.el-footer {
  text-align: center;
  line-height: 60px;
  background-color: rgba(90, 100, 101, 0.83);
  color: white;
  a {
    text-decoration: none;
    color: white;
  }
}
.sidebar-bg {
  height: 100%;
  width: 100%;
  background-color: #000;
  opacity: 0.5;
  z-index: 1000;
  position: absolute;
}
.sidebar-hidden {
  display: none;
}
.no-animate {
  .main-wrapper,
  .sidebar-wrap {
    transition: none;
  }
}
@media (min-width: 320px) and (max-width: 768px) {
  .sidebar-wrap {
    position: fixed;
    z-index: 1001;
    width: 200px;
  }
  .sidebar-wrap {
    transition: transform 0.28s;
    /* transition-timing-function: ease-in-out; */
  }
}
@media (min-width: 743px) {
  .sidebar-wrap {
    width: 200px;
    position: fixed;
    top: 0;
    bottom: 0;
    transition: width 0.28s;
    transition-timing-function: ease-in-out;
  }
  .collapse {
    position: fixed;
    width: 64px;
    transition: width 0.28s;
    transition-timing-function: ease-in-out;
  }
  .el-menu--collapse {
    width: 100%;
  }
  .main-wrapper {
    transition: margin-left 0.28s;
  }
  .sidebar-wrap ~ .main-wrapper {
    margin-left: 200px;
    transition-timing-function: ease-in-out;
  }
  .collapse ~ .main-wrapper {
    margin-left: 64px;
    transition-timing-function: ease-in-out;
  }
}
.mobile-collapse {
  transform: translate3d(-210px, 0, 0);
  transition: transform 0.28s;
}
</style>