<template>
    <el-menu
      :default-active="activeMenu"
      router
      background-color="#304156"
      text-color="hsla(0,0%,100%,.7)"
      active-text-color="#409EFF"
      :collapse="isCollapse"
      class="sidebar"
     :collapse-transition="false"
    >
      <menu-item v-for="route in permittedRoutes" :key="route.path" :item="route"></menu-item>
      <!-- <button @click="changeCollapse">test</button> -->
    </el-menu>
</template>
<script>
import { mapGetters } from "vuex";
import menuItem from "./item";


export default {
  name: "SideBar",
  components: { menuItem },
  computed: {
    ...mapGetters(["permittedRoutes","sidebar",'device']),
    activeMenu() {
      if (this.$route.path.includes("class")) {
        return "/manage/class/";
      } else {
        return this.$route.path;
      }
    },

    isCollapse:function(){
      return this.sidebar
    }

  },

  
};
</script>

